input[type="checkbox"] {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  margin: 0px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
}

#_3rd-toggle-btn span {
  display: block;
  width: 75px;
  height: 20px;
  background-color: #e0e0e0;
  border-radius: 20px;
  transition: 0.5s ease background-color;
}

#_3rd-toggle-btn span:before {
  content: "";
  position: absolute;
  bottom: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0px 0px 10px #d7d7d7;
  transform: translate(0px, 0px);
  transition: 0.6s ease transform, 0.6s box-shadow;
}

#_3rd-toggle-btn span:after {
  content: "";
  position: absolute;
  width: 0px;
  left: 16px;
  height: 12px;
  border: 4px solid rgb(57, 255, 20);
  box-shadow: 0px 0px 6px rgb(123, 255, 0);
  transition: 0.4s ease width, 0.4s ease border-radius, 0.62s ease transform;
}

#_3rd-toggle-btn input[type="checkbox"]:checked + span:before {
  transform: translate(37px, 0px);
  background-color: grey;
  box-shadow: none;
}

#_3rd-toggle-btn input[type="checkbox"]:checked + span:after {
  width: 12px;
  border-radius: 50%;
  transform: translate(31px, 0px);
  border: 4px solid black;
  box-shadow: none;
}

#_3rd-toggle-btn input[type="checkbox"]:checked + span {
  background-color: rgb(31, 31, 31);
}

