@-webkit-keyframes skew {
  0% {
    transform: skewX(6deg) translate(-6px);
  }

  50% {
    transform: skewX(-6deg) translate(6px);
  }

  100% {
    transform: skewX(6deg) translate(-6px);
  }
}

@keyframes skew {
  0% {
    transform: skewX(6deg) translate(-6px);
  }

  50% {
    transform: skewX(-6deg) translate(6px);
  }

  100% {
    transform: skewX(6deg) translate(-6px);
  }
}

@-webkit-keyframes sway {
  0% {
    transform: rotate(4deg);
  }

  50% {
    transform: rotate(-4deg);
  }

  100% {
    transform: rotate(4deg);
  }
}

@keyframes sway {
  0% {
    transform: rotate(4deg);
  }

  50% {
    transform: rotate(-4deg);
  }

  100% {
    transform: rotate(4deg);
  }
}

@-webkit-keyframes swaybot {
  0% {
    transform: rotate(-4deg);
  }

  50% {
    transform: rotate(4deg);
  }

  100% {
    transform: rotate(-4deg);
  }
}

@keyframes swaybot {
  0% {
    transform: rotate(-4deg);
  }

  50% {
    transform: rotate(4deg);
  }

  100% {
    transform: rotate(-4deg);
  }
}

